@import "../../../data/styles.css";

.article {
	display: flex;
}

.article a {
	text-decoration: none;
}

.article-left-side {
	min-width: 30%;
	max-width: 30%;
}

.article-right-side {
	flex-basis: 80%;
	min-width: 700px;
	mix-blend-mode: normal;
	border-radius: 20px;
	opacity: 0.8;
	padding: 30px;
}

.article-right-side:hover {
	background: #fafafa;
	opacity: 1;
	transition: background-color 0.3s ease-in-out;
}

.article-date {
	padding-top: 0px;
	font-size: 0px;
	color: var(--tertiary-color);
}

.article-title {
	color: #000000;
	font-size: 15px;
	/* font-weight: 600; */
}

.article-description {
	padding-top: 10px;
	font-size: 12px;
	color: var(--secondary-color);
	font-weight: 480;
	line-height: 25px;
}

.article-link {
	padding-top: 10px;
	font-size: 14px;
	color: var(--link-color);
	font-weight: 700;
}

.article-image-container {
	width: 0%;
	margin-left: -10px;
	min-width: 300px;
    max-width: 300px;
	margin-top: 5px;
}

.article-image-wrapper {
	overflow: hidden;
	border-radius: 10%;
}

.article-image-wrapper img {
	width: 100%;
}

@media (max-width: 1024px) {
	.article-left-side {
		min-width: 10%;
		max-width: 10%;
	}

	.article-right-side {
		flex-basis: 90%;
		min-width: 85%;
		max-width: 85%;
		margin-left: 10px;
	}

	.article-image-container {
		margin-left: -35px;
		margin-right: 5px;
		order: 1;
	}
}

@media (max-width: 768px) {
    .article {
        flex-direction: column;
    }

    .article-left-side, .article-right-side {
        /* Adjust width to take full width on smaller screens */
        min-width: 100%;
        max-width: 100%;
    }

    .article-image-container {
        /* Adjust width and margin for the image container */
        width: 100%;
        min-width: unset;
        max-width: unset;
        margin: 0 auto 15px; /* Add space below the image */
        order: -1; /* Places the image container above the text */
    }

    .article-right-side {
        /* Additional styling for the text container if needed */
    }
}
